import React, {Fragment} from 'react';
import {setCurrentLanguage, setAuthLanguageModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";

const LangDropdown = (props) => {
    let currentLang = props.appState.appLanguage.currentAppLanguage;
    let langListClone = [...props.appState.appLanguage.languagesInterface];
    let component = null;

    const clickHandler = ( newLangVal ,e ) => {

        props.setCurrentLanguage(newLangVal);
        props.setAuthLanguageModal(false);


    };

    let langListContent = langListClone.map((item)=>{
        let current = '';
        if(item.value === currentLang){
            current = 'currentLang';
        }
        return (<div key={item.title.toString()} onClick={(e)=> { clickHandler(item.value, e) }} className={current}><img src={item.icon} alt=""/> {item.title}</div>)
    });
    let currentLangObj = langListClone.find((langObj) => {
        return langObj.value === currentLang;
    });



    if(props.appState.modals.authLanguageDropdown) {
        component = ( <div className="appLanguageDropdown">
            <div className="content">
                {langListContent}
            </div>
        </div>);
    }

    return component;
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLanguage: (val) => dispatch(setCurrentLanguage(val)),
        setAuthLanguageModal: (bool) => dispatch(setAuthLanguageModal(bool))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LangDropdown);