import React from 'react';
import {IconPack} from '../../Images/IconPack';

import LangDropdown from './LangDropdown/LangDropdown';

import {connect} from "react-redux";
import {setAuthLanguageModal} from "../../redux/app/app.actions";
import './style.scss';

const AuthWindow = (props) => {

    let modalWindow = null;

    //        authModals: {
    //             resetPass: false,
    //             setNewEmail: false,
    //             setNewPassword: false,
    //             signIn: true,
    //             signUp: false,
    //         },



    let currentLang = props.appState.appLanguage.currentAppLanguage;
    let langListClone = [...props.appState.appLanguage.languagesInterface];


    let langListContent = langListClone.map((item)=>{
        let current = '';
        if(item.value === currentLang){
            current = 'currentLang';
        }

        return (<div key={item.toString()} className={current}><img src={item.icon} alt=""/> {item.title}</div>)
    });

    let currentLangObj = langListClone.find((langObj) => {
        return langObj.value === currentLang;
    });

    if(modalWindow) {
       return (<div className="AuthWindow">
           <div className="logo">
               {IconPack.menuIcons.logo}
           </div>
           {modalWindow}
           <div className="appLanguage appLanguage--auth" onClick={() => {props.setAuthLanguageModal(true)}}>
               <img src={currentLangObj.icon} alt=""/>
               {currentLangObj.value}
           </div>
           <LangDropdown />
       </div>);
    }else {
        return null;
    }
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setAuthLanguageModal: (bool) => dispatch(setAuthLanguageModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AuthWindow);